@import 'styles/variables';

@import './styles/reset';

.app {
	display: grid;
	grid-template-rows: auto 1fr auto;
	background-color: $main-app-bg;
	height: 100vh;
	color: $main-app-text-color;
	&--page {
		max-width: 960px;
		display: grid;
		margin: 2vmin auto 0 auto;
		padding: 0 $margin * 2;
		height: 68vh;
	}
}
